header {
  height: 20em;
  display: flex;
  align-content: center;
  flex-direction: column;
  align-items: center;
  padding: 2em 1.5em;
  background-image: url("../images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  gap: 2em;
}
header .ip_input {
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 10px;
}
header .ip_input input {
  width: 100%;
  border: none;
  outline: none;
  padding: 1.5em 1em;
  height: 0;
  overflow: hidden;
  font-size: 1.125rem;
}
header .ip_input button {
  position: absolute;
  background-color: black;
  width: 3.5em;
  background-image: url("../images/icon-arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  height: 100%;
}
header h2 {
  color: white;
  font-size: 1.6rem;
  font-weight: 500;
}
@media (min-width: 1200px) {
  header {
    height: 14em;
  }
  header .ip_input {
    width: 35em;
  }
}

