@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,500&display=swap');


* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Rubik;
    font-size: inherit;
}

body {
    min-width: 100vw;
    min-height: 100vh;
    overflow-x: hidden;
}

@media (max-width:460px) {
    ::placeholder {
        font-size: .8rem;
    }
}